import {AuthService} from './auth.service';
import {Injectable} from "@angular/core";
import {Request, RequestMethod, Http} from "@angular/http";
import {Observable} from "rxjs/Observable";
import {Product} from "./product.model";
import {Cart} from "./cart.model";
import {Email} from './email.model';
import {Message} from './message.model';
import {Order} from "./order.model";
import {Usuario} from './usuario.model';
import {stringify} from 'querystring';
import "rxjs/add/operator/map";
import {AuthHttp} from 'angular2-jwt';

const PROTOCOL = "http";
const PORT = 8080;


@Injectable()
export class RestDataSource {

  baseUrl: string;

  user: Usuario;

  constructor(private httpSecure: AuthHttp, private auth: AuthService, private httpNormal: Http) {

    console.log("location.hostname: " + location.hostname);
    this.baseUrl = `${PROTOCOL}://${location.hostname}:${PORT}/`;
  }

  getProducts(): Observable<Product[]> {
    return this.sendRequest(RequestMethod.Get, `_ah/api/store/v1/consultarProducts`,null, false, true);
  }
  

  saveOrder(order: Order): Observable<Order> {
    console.log(JSON.stringify(order));
    return this.sendRequest(RequestMethod.Post, "_ah/api/store/v1/saveOrder", order);
  }

  /**************************/

  saveProduct(product: Product): Observable<Product> {
    
    console.log("producto: " + JSON.stringify(product));    
    
    return this.sendRequest(RequestMethod.Post, "_ah/api/store/v1/saveProduct", product, true);
  }

  updateProduct(product): Observable<Product> {
    return this.sendRequest(RequestMethod.Put, "_ah/api/store/v1/updateProduct", product, true);
  }

  deleteProduct(id: number): Observable<Product> {
    return this.sendRequest(RequestMethod.Delete, `_ah/api/store/v1/deleteProduct/${id}`, null, true);
  }

  getOrders(): Observable<Order[]> {
    return this.sendRequest(RequestMethod.Get,
      "_ah/api/store/v1/getOrders", null, true, true);
  }
  deleteOrder(id: number): Observable<Order> {
    return this.sendRequest(RequestMethod.Delete,
      `_ah/api/store/v1/deleteOrder/${id}`, null, true);
  }
  updateOrder(order: Order): Observable<Order> {
    return this.sendRequest(RequestMethod.Put,
      "_ah/api/store/v1/updateOrder", order, true);
  }


  /*************************/



  authenticate(email: String): Observable<boolean> {
    return this.httpSecure.request(new Request({
      method: RequestMethod.Post,
      url: this.baseUrl + "_ah/api/store/v1/authUser",
      body: {mail: email}
    })).map(response => {
      let r = response.json();
      this.user = r.success ? r.token : null;
      return r.success;
    });
  }


  private sendRequest(
    verb: RequestMethod,
    url: string,
    body?: any,
    asegurada = false,
    listado = false
  ): Observable<any> {

    let request = new Request({
      method: verb,
      url: this.baseUrl + url,
      body: body
    });

    let http: any;


    if (asegurada) {
      console.log("segura");
      http = this.httpSecure;
    }
    else {
      console.log("normal");
      http = this.httpNormal;
    }

    return http.request(request).map(response => (listado) ? response.json().items : response.json());

  }


}


